<template lang='pug'>
div
  div.row
    div.col-xs-12.text-center
      h3 Unmatched
  div.row
    div.col-xs-1
      h4 Men:
    div.col-xs-11
      div.personBox.m(
        v-for='m in unmatched.men' 
        :style='{"background-color": colors[m]}'
      ) 
        p m
          sub {{m+1}}
  div.row
    div.col-xs-1
      h4 Women:
    div.col-xs-11
      div.personBox.w(
        v-for='w in unmatched.women' 
        :style='{"background-color": colors[w]}'
      ) 
        p w
          sub {{w+1}}
</template>

<script>
export default {
  props: [
    'n',
    'colors',
    'unmatched'
  ],
  // end props
  data () {
    return {

    }
  }
}
</script>

<style scoped>
h4 {
  padding-top: 8px;
  text-align-last: right;
}
div.row {
  margin-bottom: 5px;
}
</style>


